import Circle from './base/circle';

const SHAPE_SIZE = 30;

class EndEventShape extends Circle {
  constructor({ position }) {
    super({ position });
    this.instance.resize(SHAPE_SIZE, SHAPE_SIZE);
    this.instance.attr('body/fill', '#cea7a7');
    this.instance.attr('body/stroke', '#a81919');
    this.instance.attr('body/stroke-width', '5');
  }
}

export default EndEventShape;
