import { shapes } from 'jointjs/dist/joint.core';
import Shape from './shape';

const defaults = {
  size: {
    x: 30,
    y: 30,
  },
};

class Circle extends Shape {
  constructor({ position, size = defaults.size }) {
    super();
    this.instance = new shapes.standard.Circle();
    this.instance.resize(size.x, size.y);
    this.instance.position(position.x, position.y);
  }
}

export default Circle;
