import Circle from './base/circle';

const SHAPE_SIZE = 30;

class StartEventShape extends Circle {
  constructor({ position }) {
    super({ position });
    this.instance.resize(SHAPE_SIZE, SHAPE_SIZE);
    this.instance.attr('body/fill', '#b1c5a4');
    this.instance.attr('body/stroke', '#48a01d');
    this.instance.attr('body/stroke-width', '1.5');
  }
}

export default StartEventShape;
