import { dia, shapes as shapesNamespace } from 'jointjs/dist/joint.core'; // https://stackoverflow.com/a/56948763
import shapes from './shapes';

class Board {
  /**
   * @param {HTMLElement} el
   */
  constructor(el) {
    this.$el = el;
    this.shapes = shapes;

    this.add = Object.keys(this.shapes).reduce((hash, method) => {
      // eslint-disable-next-line no-param-reassign
      hash[method] = (opts) => {
        const shape = this.shapes[method](opts);
        shape.addTo(this.graph);
        return shape;
      };
      return hash;
    }, {});
  }

  setupShapes(graph) {
    this.add = Object.keys(this.shapes).reduce((hash, method) => {
      // eslint-disable-next-line no-param-reassign
      hash[method] = (opts) => {
        const shape = this.shapes[method](opts);
        shape.addTo(graph);
        return shape;
      };
      return hash;
    }, {});
  }

  makeGraph(options = {}) {
    return new dia.Graph(options, { cellNamespace: shapesNamespace });
  }

  init(graph) {
    this.graph = graph || this.makeGraph();
    this.paper = new dia.Paper({
      el: this.$el,
      width: this.$el.clientWidth,
      height: this.$el.clientHeight,
      gridSize: 1,
      model: this.graph,
      cellViewNamespace: shapesNamespace,
    });
    this.setupShapes(this.graph);
    return this;
  }

  clear() {
    this.graph.clear();
  }

  export() {
    return this.graph.toJSON();
  }

  from(cells) {
    const graph = this.makeGraph();
    return this.init(graph.fromJSON(cells));
  }
}

export default Board;
