import { shapes } from 'jointjs/dist/joint.core';
import Shape from './shape';

class Link extends Shape {
  constructor({ source, target }) {
    super();
    const link = new shapes.standard.Link();
    link.source(source.instance);
    link.target(target.instance);
    link.attr({
      line: {
        strokeWidth: 1,
        stroke: '#222222',
      },
    });

    this.instance = link;
  }
}

export default Link;
